import * as $ from 'jquery';

$(document).ready(function () {
  const header = document.querySelector('header');
  const headerBot = document.querySelector('.header__bot');

  window.addEventListener('scroll', function () {
    let scrollTop = 130;
    let scrollHeadNav = 700;
    let scrollPos = window.scrollY;

    if (scrollPos > scrollTop) {
      header.classList.add('sticky');
    } else {
      header.classList.remove('sticky');
    }

    if (scrollPos > scrollHeadNav) {
      headerBot.classList.add('act');
    } else {
      headerBot.classList.remove('act');
    }
  });

  const arrowLeft = document.querySelector('.arrow__left');
  const arrowRight = document.querySelector('.arrow__right');
  const fileSlide = document.querySelector('.files__slide');

  arrowLeft.addEventListener('click', function () {
    fileSlide.classList.remove('act');
  });
  arrowRight.addEventListener('click', function () {
    fileSlide.classList.add('act');
  });

  const headerBurger = document.querySelector('.header__burger');
  const navMobile = document.querySelector('.nav__mobile');
  const closeNav = document.querySelector('.close__nav');
  const mBody = document.querySelector('body');

  headerBurger.addEventListener('click', function () {
    navMobile.classList.add('active');
    mBody.classList.add('ov');
  });

  closeNav.addEventListener('click', function () {
    navMobile.classList.remove('active');
    mBody.classList.remove('ov');
  });

  const scrollBlock = document.querySelectorAll('.header__bot .menu span');
  for (let navItem of scrollBlock) {
    navItem.addEventListener('click', function () {
      const el = document.getElementById('files__anchor');
      el.scrollIntoView({ behavior: 'smooth' });
    });
  }

  $(function () {
    var $itemsHead = $('.nav-tab-head');
    $itemsHead
      .click(function () {
        $itemsHead.removeClass('active');
        $('.files__header li').removeClass('active');
        $(this).addClass('active');
        let nm = $(this).parent().attr('class').slice(-1);
        $('.files__header li span.tb-0' + nm)
          .parent()
          .addClass('active');
        var index = $itemsHead.index($(this));
        $('.files__body .tab-pane')
          .removeClass('active')
          .eq(index)
          .addClass('active');
        return false;
      })
      .eq(0)
      .click();

    var $items = $('.nav-tab');
    $items
      .click(function () {
        $items.removeClass('active');
        $('.menu .menu__link').removeClass('active');
        $(this).addClass('active');
        let nm = $(this).children().attr('class').slice(-1);
        $('.menu li.link-0' + nm)
          .children()
          .addClass('active');
        var index = $items.index($(this));
        $('.files__body .tab-pane')
          .removeClass('active')
          .eq(index)
          .addClass('active');
        return false;
      })
      .eq(0)
      .click();
  });

  $('.accordion__nav').click(function () {
    if ($(this).parent().is('.active')) {
      $('.accordion_wrap').removeClass('active');
    } else {
      $('.accordion_wrap').removeClass('active');
      $(this).parent().addClass('active');
      let nm = $(this).attr('class').slice(-1);
      const el2 = document.getElementById('accordion__nav-0' + nm);
      el2.scrollIntoView({ behavior: 'smooth' });
    }
  });

  $('.nav__mobile_wrap li').click(function () {
    navMobile.classList.remove('active');
    mBody.classList.remove('ov');
    $('.accordion_wrap').removeClass('active');
    let nm = $(this).attr('class').slice(-1);
    const el = document.getElementById('accordion__nav-0' + nm);
    $('.accordion__nav-0' + nm)
      .parent()
      .addClass('active');
    el.scrollIntoView({ behavior: 'smooth' });
  });

  $('.send__files_button').click(function () {
    $(this).parent().parent().addClass('open');
    mBody.classList.add('ov');
  });

  $('.close__form').click(function () {
    $('.files__body_row').removeClass('open');
    mBody.classList.remove('ov');
  });
});
